<template>
  <div>
    <van-sticky>
      <div class="topTab">
        <van-icon name="arrow-left" size="20" @click="$router.back()" />
        <div class="title">问题详情</div>
      </div>
      <div class="productInfo">
        <img :src="problemDetail?.product_img_url" alt="" />
        <div>{{ problemDetail?.product_name }}</div>
      </div>
    </van-sticky>
    <div class="question">
      <p>{{ problemDetail?.question }}</p>
      <div class="user">
        <img :src="problemDetail?.avatar" alt="" />
        <div>
          <p class="nickname">{{ problemDetail?.nickname }}</p>
          <span
            >提问于{{ problemDetail?.create_time }}
            {{ problemDetail?.belong_place }}</span
          >
        </div>
        <div class="care">关注问题</div>
      </div>
    </div>

    <div class="answer">
      <div class="answer-header">
        <span> 共 {{ problemDetail?.answer_num }} 条讨论</span>
      </div>
      <div class="switch">
        <span :style="!checked ? 'color:#ff5934' : ''">热门</span
        ><van-switch
          v-model="checked"
          active-color="#dcdee0"
          inactive-color="#dcdee0"
          size="16px"
          @click="getproblemAnswerList"
        />
        <span :style="checked ? 'color:#ff5934' : ''">最新</span>
      </div>
    </div>

    <van-list
      v-model="loading"
      :finished="finished"
      finished-text="没有更多了"
      @load="onLoad"
    >
      <div class="answerList">
        <div v-for="(answer, index) in problemAnswerList" :key="index">
          <div class="answerUser">
            <img :src="answer.avatar" alt="" />
            <div>
              <div style="display: flex">
                <div class="answerUserInfo">
                  <p>
                    {{ answer.nickname }} {{ answer.create_time }}
                    {{ answer.belong_place }}
                  </p>
                  <p class="content">{{ answer.answer_content }}</p>
                </div>
                <div style="display: flex; margin-top: 0.2rem">
                  <div ref="goodjob">
                    <van-icon name="good-job-o" @click="goodJob" />
                  </div>
                  <span style="margin-left: 8px">{{
                    answer.support || 0
                  }}</span>
                </div>
              </div>
              <div class="secondary_data">
                <div
                  v-for="(sanswer, index) in answer?.secondary_data
                    ?.answer_list"
                  :key="index"
                >
                  <div class="item">
                    <div>
                      <span>{{ sanswer.nickname }}:</span>
                      <span class="content">{{ sanswer.answer_content }}</span>
                    </div>
                    <span>{{ sanswer.belong_place }}</span>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </van-list>

    <footer>
      <div class="bottom-btn" @click="bottomBtn">
        <img src="@/assets/pl.png" alt="" />
        一起讨论吧
      </div>
      <div style="display: flex; align-items: center">
        <img src="@/assets/m_b_share.ac01313399.png" alt="" />
        <div style="margin-left: 0.8rem">
          <van-icon name="good-job-o" size="0.4rem" color="#818181" />
          {{ problemDetail?.praise }}
        </div>
      </div>
    </footer>
  </div>
</template>

<script>
import { Toast } from "vant";
export default {
  data() {
    return {
      checked: false,
      loading: false,
      finished: false,

      problemDetail: null,
      problemAnswerList: null,
    };
  },
  methods: {
    getProblemDetail() {
      this.axios
        .get(
          `communicate/question_detail?q_id=${this.$route.query.q_id}&product_id=${this.$route.query.product_id}`
        )
        .then((res) => {
          console.log(res.data.data);
          this.problemDetail = res.data.data;
        });
    },
    getproblemAnswerList() {
      this.$nextTick(() => {
        if (this.checked) {
          this.axios
            .get(
              `communicate/question_answer_list?q_id=${this.$route.query.q_id}&top_answer_id&size=10&after&page=1&need_secondary=true&is_hot=false`
            )
            .then((res) => {
              console.log(res.data.data.answer_list);
              this.problemAnswerList = res.data.data.answer_list;
            });
        } else {
          this.axios
            .get(
              `communicate/question_answer_list?q_id=${this.$route.query.q_id}&top_answer_id&size=10&after&page=1&need_secondary=true&is_hot=true`
            )
            .then((res) => {
              console.log(res.data.data.answer_list);
              this.problemAnswerList = res.data.data.answer_list;
            });
        }
      });
    },
    bottomBtn() {
      Toast("请前往app参与");
    },
    goodJob() {
      Toast("请前往app参与");
    },
    // 触底
    onLoad() {
      this.loading = true;
      // 异步更新数据
      // setTimeout 仅做示例，真实场景中一般为 ajax 请求
      setTimeout(() => {
        if (this.problemAnswerList) {
          this.problemAnswerList = [
            ...this.problemAnswerList,
            ...this.problemAnswerList,
          ];
        }

        // 加载状态结束
        this.loading = false;

        // 数据全部加载完成
        if (this.problemAnswerList?.length >= 40) {
          this.finished = true;
        }
      }, 1000);
    },
  },
  activated() {
    this.getProblemDetail();
    this.getproblemAnswerList();
  },
};
</script>

<style lang="less" scoped>
.topTab {
  padding: 0 0.32rem;
  height: 0.88rem;
  display: flex;
  justify-content: space-around;
  align-items: center;
  background-color: rgb(242, 242, 242);
  .title {
    width: 90vw;
    text-align: center;
  }
}
.productInfo {
  font-size: 0.24rem;
  color: #000;
  line-height: 0.28rem;
  display: flex;
  align-items: center;
  height: 0.8rem;
  background: #fafafa;
  padding: 0 0.32rem;
  img {
    width: 0.43rem;
    height: 0.57rem;
    margin-right: 0.3rem;
  }
}
.question {
  background: #fff;
  padding: 0.26rem 0.32rem 0.32rem;
  text-align: left;
  p {
    margin: 0;
    font-size: 0.32rem;
    line-height: 0.48rem;
  }
  .user {
    display: flex;
    margin-top: 0.32rem;
    align-items: center;
    img {
      width: 0.48rem;
      height: 0.48rem;
      border-radius: 0.24rem;
      margin-right: 0.2rem;
    }
    .nickname {
      font-size: 0.24rem;
    }
    span {
      font-size: 0.2rem;
      color: rgba(0, 0, 0, 0.3);
      padding-top: 0.05rem;
    }
    .care {
      width: 1.28rem;
      height: 0.48rem;
      background: rgba(255, 89, 52, 0.07);
      border-radius: 0.24rem;
      font-size: 0.24rem;
      color: #ff5934;
      line-height: 0.48rem;
      text-align: center;
      margin: 0.24rem 0 0 auto;
    }
  }
}
.answer {
  height: 0.88rem;
  line-height: 0.88rem;
  border-bottom: 1px solid hsla(0, 0%, 59%, 0.2);
  padding: 0 0.32rem;
  font-size: 0.24rem;
  display: flex;
  justify-content: space-between;
  .answer-header {
    color: rgba(0, 0, 0, 0.4);
  }
  .switch {
    display: flex;
    align-items: center;
    .van-switch {
      margin: 0 8px;
    }
  }
}
.answerList {
  padding: 0 0.32rem;
  img {
    width: 0.52rem;
    height: 0.52rem;
    border-radius: 50%;
    margin-right: 0.2rem;
    overflow: hidden;
    margin-top: 0.2rem;
  }
  .answerUser {
    display: flex;
    justify-content: space-between;
    .answerUserInfo {
      margin-bottom: 0.06rem;
      font-size: 0.24rem;
      color: #999;
      .content {
        font-size: 0.26rem;
        font-weight: 400;
        color: #333;
        margin: 0.06rem 0 0.2rem;
        line-height: 1.6;
        width: 70vw;
      }
    }
    .secondary_data {
      background: #fafafa;
      border-radius: 0.08rem;
      padding: 0.2rem 0.2rem 0;
      margin-bottom: 0.27rem;
      font-size: 0.24rem;
      .item {
        span {
          color: rgba(0, 0, 0, 0.5);
          margin-right: 0.1rem;
        }
        .content {
          padding-bottom: 0.2rem;
          color: rgba(0, 0, 0, 0.87);
        }
      }
    }
  }
}

footer {
  display: flex;
  justify-content: space-around;
  align-items: center;
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  padding: 0.15rem 0 0.15rem 0.24rem;
  background-color: #fff;
  border-top: 1px solid #f5f5f5;
  z-index: 999;
  .bottom-btn {
    width: 45vw;
    height: 0.66rem;
    line-height: 0.66rem;
    border-radius: 0.66rem;
    padding: 0 0.16rem;
    background: #f6f6f6;
    font-weight: 500;
    color: #999;
    font-size: 0.26rem;
    text-align: left;
    display: flex;
    align-items: center;
    img {
      display: inline-block;
      margin-right: 0.09rem;
      width: 0.34rem;
      height: 0.34rem;
    }
  }
  img {
    width: 0.4rem;
    height: 0.4rem;
    margin-right: 8px;
  }
}
</style>